.column-container {
    padding: .15rem;
}


.header-container {
    padding: .5rem;
    min-width: 12rem;
    max-width: 17rem;
}



.header {
    font-size: .75rem;
}



.header-icon {
    margin-right: .5rem;
}

.add {
    margin-right: .5rem;
    transition: .25s;
    cursor: pointer;
}

.three-dot {
    transform: .25s;
    cursor: pointer;
}

.add:hover {
    transform: scale(1.2);
}

.three-dot:hover {
    transform: scale(1.2);

}
